@import url("https://fonts.googleapis.com/css?family=Roboto:400,700");
* {
  font-family: 'Roboto', sans-serif;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
body {
  margin: 0;
  background: #eee;
}
.content {
  width: calc(100% - 336px);
  position: absolute;
  margin-left: 300px;
  padding: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}
.sidebar {
  -webkit-box-shadow: 3px 0 3px rgba(0,0,0,0.1);
          box-shadow: 3px 0 3px rgba(0,0,0,0.1);
  position: fixed;
  width: 300px;
  background: #fff;
  height: 100%;
  overflow: auto;
}
.sidebar::-webkit-scrollbar {
  width: 2px;
}
.sidebar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 rgba(0,0,0,0);
}
.sidebar::-webkit-scrollbar-thumb {
  background-color: transparent;
}
.sidebar li {
  list-style-type: none;
  padding: 16px;
  margin-left: 8px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.sidebar li:last-of-type {
  border: none;
}
.sidebar li span {
  vertical-align: middle;
}
.sidebar li div {
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background: #3f51b5;
  display: inline-block;
  vertical-align: middle;
  font-weight: 500;
}
.sidebar li.active {
  background: #f6f7fc;
}
.sidebar li.active span {
  font-weight: bold;
  color: #3f51b5;
}
.sidebar li:hover:not(.active) {
  padding-left: 32px;
  background: rgba(0,0,0,0.02);
}
.card {
  margin: 16px;
  background: #fff;
  -webkit-box-shadow: 0 3px 2px rgba(0,0,0,0.1);
          box-shadow: 0 3px 2px rgba(0,0,0,0.1);
  border-radius: 4px;
  padding: 16px;
  min-width: 40%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.card h4 {
  font-size: 16px;
  margin: 0;
  color: #3f51b5;
  padding: 8px;
  display: inline-block;
  background: #f6f7fc;
  text-transform: uppercase;
}
.card .actions {
  margin: 0 4px;
  text-align: right;
}
.card .actions .button {
  font-weight: bold;
  font-size: 14px;
  color: #fff;
  background: #3f51b5;
  padding: 10px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 16px;
  cursor: pointer;
}
.card .actions .button:hover {
  background: #3949a3;
}
.result-container {
  margin-top: 16px;
}
.result {
  color: #fff;
  background: #363636;
  padding: 16px;
  margin-top: 16px;
  word-wrap: break-word;
  position: relative;
}
.time {
  margin-left: 8px;
  padding: 8px;
  color: #3f51b5;
  border-radius: 2px;
  border: 2px solid #f6f7fc;
}
.part {
  background: #606060;
  border-radius: 6px;
  padding: 8px;
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 0;
}
.part .part {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  text-align: center;
  line-height: 18px;
  margin: 0;
  vertical-align: middle;
}
.part .part:first-of-type {
  background: #3f51b5;
}
table {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  table-layout: fixed;
}
td:nth-child(1) {
  color: #f6f7fc;
  border: 2px solid #2bb673;
  padding: 8px;
  margin-top: 8px;
  margin-bottom: 8px;
  display: inline-block;
}
td:nth-child(2) {
  margin-top: 8px;
  margin-bottom: 8px;
  display: block;
}
td:nth-child(2) div {
  display: inline-block;
}
td:nth-child(2) .comma {
  margin-right: 8px;
}
td:nth-child(2) .res {
  width: 100%;
  word-break: break-all;
}
.codebook {
  -ms-flex-direction: row;
      flex-direction: row;
  max-width: 100%;
}
.codebook div {
  display: inline-block;
  margin-right: 8px;
  margin-bottom: 8px;
  padding: 8px;
  padding-left: 0;
  background: rgba(255,255,255,0.1);
}
.codebook div span:first-of-type {
  background: #3f51b5;
  padding: 8px;
  margin-right: 8px;
}
tr:last-of-type td:last-of-type,
tr:last-of-type td:last-of-type div {
  margin-bottom: 0;
}
.sardinas {
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-direction: row;
      flex-direction: row;
}
.column {
  margin-right: 16px;
  text-align: center;
  width: 60px;
  border-radius: 4px;
  border: 2px solid rgba(255,255,255,0.5);
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 8px;
}
.column span {
  margin: 8px;
}
.status {
  max-width: 48px;
}
.header {
  text-align: right;
  padding: 10px;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  background: #fafafa;
  margin-bottom: 8px;
  color: #3f51b5;
}
.white {
  background: #fff;
  padding: 16px;
}
.green {
  color: #2bb673 !important;
}



